import React, { useState } from 'react';
import './App.css';
import PortfolioBuilder from './components/PortfolioBuilder';
import MethodologyDoc from './components/MethodologyDoc';

function App() {
  const [currentView, setCurrentView] = useState('builder');

  const renderView = () => {
    switch (currentView) {
      case 'methodology':
        return <MethodologyDoc />;
      case 'builder':
      default:
        return <PortfolioBuilder />;
    }
  };

  return (
    <div className="App">
      <div className="app-container">
        <header className="app-header">
          <h1>Value Investing Portfolio Builder</h1>
          <p className="subtitle">
            Construye tu portafolio de inversión basado en principios de Warren Buffett
          </p>
          
          <nav style={{ 
            marginTop: '20px',
            display: 'flex',
            gap: '16px',
            justifyContent: 'center'
          }}>
            <button 
              className={`btn ${currentView === 'builder' ? '' : 'btn-secondary'}`}
              onClick={() => setCurrentView('builder')}
            >
              Construir Portafolio
            </button>
            <button 
              className={`btn ${currentView === 'methodology' ? '' : 'btn-secondary'}`}
              onClick={() => setCurrentView('methodology')}
            >
              Metodología
            </button>
          </nav>
        </header>
        
        <main className="main-content">
          {renderView()}
        </main>

        <footer style={{ 
          textAlign: 'center', 
          padding: '20px', 
          color: 'rgba(255, 255, 255, 0.8)',
          fontSize: '0.9rem'
        }}>
          <p>© 2024 Value Investing Portfolio Builder | Basado en principios de inversión de valor</p>
        </footer>
      </div>
    </div>
  );
}

export default App;
