import React, { useState } from 'react';

const MethodologyDoc = () => {
  const [expandedSections, setExpandedSections] = useState({});

  const toggleSection = (sectionId) => {
    setExpandedSections(prev => ({
      ...prev,
      [sectionId]: !prev[sectionId]
    }));
  };

  const sections = [
    {
      id: 'value-investing-intro',
      title: 'Introducción al Value Investing',
      content: `
        El Value Investing es una estrategia de inversión desarrollada por Benjamin Graham y popularizada por Warren Buffett. 
        Se basa en la idea de que el mercado a veces valora incorrectamente las empresas, creando oportunidades para 
        comprar acciones por debajo de su valor intrínseco.

        **Principios fundamentales:**
        - Invertir en empresas, no en acciones
        - Buscar un margen de seguridad
        - Pensar a largo plazo
        - Ser paciente y disciplinado
        - Entender el negocio antes de invertir
      `
    },
    {
      id: 'intrinsic-value',
      title: 'Valor Intrínseco',
      content: `
        El valor intrínseco es el valor real de una empresa basado en sus fundamentos financieros, 
        independientemente de su precio actual en el mercado.

        **Cálculo del valor intrínseco:**
        - Análisis de flujos de caja descontados (DCF)
        - Múltiplos de valoración (P/E, P/B, EV/EBITDA)
        - Valor en libros ajustado
        - Comparación con empresas similares

        **Factores clave:**
        - Crecimiento sostenible de ingresos
        - Márgenes de beneficio estables
        - Generación de flujo de caja libre
        - Calidad del balance
      `
    },
    {
      id: 'margin-of-safety',
      title: 'Margen de Seguridad',
      content: `
        El margen de seguridad es la diferencia entre el precio de mercado de una acción y su valor intrínseco estimado. 
        Es el concepto más importante en value investing.

        **¿Por qué es importante?**
        - Protege contra errores en la valoración
        - Reduce el riesgo de pérdidas
        - Maximiza el potencial de retorno
        - Proporciona tranquilidad al inversor

        **Margen recomendado:**
        - Mínimo 20-30% para empresas estables
        - 40-50% para empresas con mayor riesgo
        - Cuanto mayor, mejor
      `
    },
    {
      id: 'competitive-moat',
      title: 'Ventaja Competitiva (Moat)',
      content: `
        Una ventaja competitiva duradera es lo que permite a una empresa mantener o incrementar su cuota de mercado 
        y rentabilidad a lo largo del tiempo.

        **Tipos de ventajas competitivas:**
        - **Marcas fuertes**: Reconocimiento y lealtad del consumidor
        - **Efectos de red**: El valor aumenta con más usuarios
        - **Ventajas de coste**: Producción más eficiente
        - **Barreras de entrada**: Regulaciones, patentes, etc.
        - **Economías de escala**: Costes unitarios menores

        **Evaluación:**
        - Rentabilidad sostenida sobre el capital (ROE > 15%)
        - Crecimiento consistente de ingresos
        - Márgenes estables o crecientes
        - Posición dominante en el mercado
      `
    },
    {
      id: 'financial-analysis',
      title: 'Análisis Financiero',
      content: `
        El análisis financiero es fundamental para evaluar la salud y el potencial de una empresa.

        **Métricas clave de rentabilidad:**
        - ROE (Return on Equity) > 15%
        - ROA (Return on Assets) > 5%
        - Margen neto > 10%
        - Crecimiento de beneficios consistente

        **Métricas de valoración:**
        - P/E ratio < 15 (para empresas estables)
        - P/B ratio < 1.5
        - PEG ratio < 1
        - EV/EBITDA < 10

        **Análisis del balance:**
        - Ratio de deuda/patrimonio < 0.5
        - Ratio corriente > 1.5
        - Flujo de caja libre positivo
        - Crecimiento del patrimonio neto
      `
    },
    {
      id: 'management-quality',
      title: 'Calidad de la Gestión',
      content: `
        La calidad del equipo directivo es crucial para el éxito a largo plazo de una empresa.

        **Indicadores de buena gestión:**
        - Transparencia en la comunicación
        - Historial de cumplimiento de objetivos
        - Asignación eficiente del capital
        - Alineación con intereses de accionistas
        - Visión a largo plazo

        **Señales de alerta:**
        - Compensaciones excesivas
        - Falta de transparencia
        - Cambios frecuentes de estrategia
        - Historial de adquisiciones destructivas de valor
        - Conflictos de interés
      `
    },
    {
      id: 'portfolio-construction',
      title: 'Construcción del Portafolio',
      content: `
        Un portafolio de value investing bien construido balancea concentración con diversificación.

        **Principios de construcción:**
        - Máximo 5-8% en una sola posición
        - Diversificación entre sectores (máximo 25% por sector)
        - Exposición geográfica equilibrada
        - Mínimo 15-20 posiciones para diversificar riesgo
        - Mantener efectivo para oportunidades (5-10%)

        **Asignación recomendada:**
        - **Bonos**: 20-40% (según edad y perfil de riesgo)
        - **Value**: 40-60% (núcleo del portafolio)
        - **Growth**: 10-30% (crecimiento futuro)
        - **Disruptivas**: 0-10% (opcional, alto riesgo)
      `
    },
    {
      id: 'risk-management',
      title: 'Gestión del Riesgo',
      content: `
        La gestión del riesgo es tan importante como la selección de inversiones.

        **Tipos de riesgo:**
        - **Riesgo de mercado**: Caídas generales del mercado
        - **Riesgo específico**: Problemas de la empresa
        - **Riesgo de valoración**: Errores en el análisis
        - **Riesgo de liquidez**: Dificultad para vender
        - **Riesgo de inflación**: Pérdida de poder adquisitivo

        **Estrategias de mitigación:**
        - Diversificación adecuada
        - Margen de seguridad suficiente
        - Horizonte de inversión largo
        - Revisión periódica del portafolio
        - Mantener reservas de efectivo
      `
    },
    {
      id: 'long-term-thinking',
      title: 'Pensamiento a Largo Plazo',
      content: `
        El value investing requiere una mentalidad a largo plazo y la capacidad de ignorar la volatilidad a corto plazo.

        **Ventajas del largo plazo:**
        - Capitalización del interés compuesto
        - Beneficios fiscales
        - Menor estrés y mejor toma de decisiones
        - Tiempo para que se materialice el valor intrínseco
        - Costes de transacción menores

        **Horizonte recomendado:**
        - Mínimo 5-7 años por inversión
        - Ideal 10+ años
        - Revisión anual del portafolio
        - Rebalanceo según sea necesario
        - Paciencia para esperar oportunidades
      `
    },
    {
      id: 'common-mistakes',
      title: 'Errores Comunes',
      content: `
        Evitar estos errores comunes puede mejorar significativamente los resultados de inversión.

        **Errores frecuentes:**
        - Falta de diversificación
        - Seguir las modas del mercado
        - Vender en pánico durante correcciones
        - Sobreestimar las propias habilidades
        - Ignorar los fundamentos por los gráficos

        **Sesgos psicológicos:**
        - Sesgo de confirmación
        - Efecto manada
        - Aversión a las pérdidas
        - Exceso de confianza
        - Anclaje en precios pasados

        **Cómo evitarlos:**
        - Mantener disciplina en la estrategia
        - Diversificar adecuadamente
        - Basar decisiones en análisis, no emociones
        - Revisar y aprender de errores pasados
        - Mantener perspectiva a largo plazo
      `
    }
  ];

  const SectionContent = ({ content }) => {
    const formatContent = (text) => {
      return text.split('\n').map((line, index) => {
        line = line.trim();
        if (line === '') return <br key={index} />;
        
        if (line.startsWith('**') && line.endsWith('**')) {
          return (
            <h4 key={index} style={{ marginTop: '16px', marginBottom: '8px', color: '#2d3748' }}>
              {line.slice(2, -2)}
            </h4>
          );
        }
        
        if (line.startsWith('- **') && line.includes('**:')) {
          const [boldPart, normalPart] = line.split('**:');
          return (
            <li key={index} style={{ marginBottom: '8px' }}>
              <strong>{boldPart.slice(3)}</strong>: {normalPart}
            </li>
          );
        }
        
        if (line.startsWith('- ')) {
          return (
            <li key={index} style={{ marginBottom: '4px' }}>
              {line.slice(2)}
            </li>
          );
        }
        
        return (
          <p key={index} style={{ marginBottom: '12px', lineHeight: '1.6' }}>
            {line}
          </p>
        );
      });
    };

    return (
      <div style={{ color: '#4a5568' }}>
        {formatContent(content)}
      </div>
    );
  };

  return (
    <div className="methodology-doc">
      <div className="card">
        <div className="card-header">
          <h2 className="card-title">Metodología de Value Investing</h2>
          <p style={{ color: '#718096', marginTop: '8px' }}>
            Guía completa sobre los principios y estrategias del value investing
          </p>
        </div>
        
        <div className="methodology-content">
          <div className="methodology-intro">
            <p style={{ fontSize: '1.1rem', lineHeight: '1.6', color: '#4a5568', marginBottom: '24px' }}>
              Esta metodología se basa en los principios establecidos por Benjamin Graham y Warren Buffett, 
              adaptados para el inversor moderno. Cada sección proporciona conocimientos prácticos 
              para implementar una estrategia de value investing exitosa.
            </p>
          </div>

          <div className="methodology-sections">
            {sections.map((section) => (
              <div key={section.id} className="methodology-section">
                <div 
                  className="section-header"
                  onClick={() => toggleSection(section.id)}
                  style={{
                    cursor: 'pointer',
                    padding: '16px',
                    background: '#f7fafc',
                    borderRadius: '8px',
                    marginBottom: expandedSections[section.id] ? '16px' : '8px',
                    border: '1px solid #e2e8f0',
                    transition: 'all 0.3s ease'
                  }}
                >
                  <h3 style={{ 
                    margin: 0, 
                    color: '#2d3748',
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center'
                  }}>
                    {section.title}
                    <span style={{ 
                      fontSize: '1.2rem',
                      transform: expandedSections[section.id] ? 'rotate(180deg)' : 'rotate(0deg)',
                      transition: 'transform 0.3s ease'
                    }}>
                      ▼
                    </span>
                  </h3>
                </div>
                
                {expandedSections[section.id] && (
                  <div 
                    className="section-content"
                    style={{
                      padding: '20px',
                      background: 'white',
                      borderRadius: '8px',
                      border: '1px solid #e2e8f0',
                      marginBottom: '16px'
                    }}
                  >
                    <SectionContent content={section.content} />
                  </div>
                )}
              </div>
            ))}
          </div>

          <div className="methodology-footer">
            <div style={{
              background: '#f0f4fa',
              padding: '20px',
              borderRadius: '8px',
              border: '1px solid #e2e8f0',
              marginTop: '24px'
            }}>
              <h4 style={{ color: '#2d3748', marginBottom: '12px' }}>
                Recursos Adicionales
              </h4>
              <ul style={{ margin: 0, paddingLeft: '20px' }}>
                <li style={{ marginBottom: '8px', color: '#4a5568' }}>
                  <strong>Libros recomendados:</strong> "El Inversor Inteligente" de Benjamin Graham, 
                  "Cartas a los Accionistas" de Warren Buffett
                </li>
                <li style={{ marginBottom: '8px', color: '#4a5568' }}>
                  <strong>Herramientas:</strong> Calculadoras de DCF, screeners de acciones, 
                  análisis de estados financieros
                </li>
                <li style={{ marginBottom: '8px', color: '#4a5568' }}>
                  <strong>Seguimiento:</strong> Reportes anuales, llamadas de resultados, 
                  análisis de analistas independientes
                </li>
                <li style={{ color: '#4a5568' }}>
                  <strong>Comunidad:</strong> Foros de inversores, conferencias, 
                  grupos de estudio de value investing
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MethodologyDoc; 