import React, { useState, useEffect, useCallback } from 'react';
import InvestmentProfile from './InvestmentProfile';
import AssetAllocation from './AssetAllocation';
import PortfolioResults from './PortfolioResults';
import ClaudeAnalysis from './ClaudeAnalysis';
import LoadingSpinner from './LoadingSpinner';
import './PortfolioBuilder.css';

const PortfolioBuilder = () => {
  const [currentStep, setCurrentStep] = useState(1);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  
  // Form data
  const [investmentProfile, setInvestmentProfile] = useState({
    amount: 10000,
    horizon: 'largo',
    riskTolerance: 'moderado',
    experience: 'intermedio'
  });
  
  const [assetAllocation, setAssetAllocation] = useState({
    bonds: 25,
    value: 50,
    growth: 25,
    disruptive: 0
  });
  
  // Portfolio data
  const [portfolioData, setPortfolioData] = useState({
    value: null,
    growth: null,
    bonds: null,
    disruptive: null
  });
  
  const [finalPortfolio, setFinalPortfolio] = useState(null);
  const [claudeAnalysis, setClaudeAnalysis] = useState('');
  const [activeTab, setActiveTab] = useState('analysis'); // 'analysis' | 'glossary'

  const BASE_URL = process.env.REACT_APP_BACKEND_URL || 'https://value-investing-5b425882ff1a.herokuapp.com';

  const steps = [
    { id: 1, title: 'Perfil', description: 'Información personal' },
    { id: 2, title: 'Asignación', description: 'Distribución de activos' },
    { id: 3, title: 'Construcción', description: 'Búsqueda de activos' },
    { id: 4, title: 'Análisis', description: 'Revisión y recomendaciones' }
  ];

  const nextStep = () => {
    if (currentStep < steps.length) {
      setCurrentStep(currentStep + 1);
    }
  };

  const prevStep = () => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1);
    }
  };

  const fetchPortfolioData = async (category, amount) => {
    setLoading(true);
    setError('');
    
    try {
      console.log(`🔍 Iniciando búsqueda en tiempo real para ${category} con $${amount.toLocaleString()}`);
      
      const response = await fetch(`${BASE_URL}/api/portfolio/${category}`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ 
          amount,
          timestamp: new Date().toISOString(),
          realTimeSearch: true 
        })
      });
      
      if (!response.ok) {
        throw new Error(`Error al obtener datos de ${category}: ${response.status}`);
      }
      
      const data = await response.json();
      console.log(`✅ Datos en tiempo real obtenidos para ${category}:`, data);
      return data;
    } catch (err) {
      console.error(`❌ Error en búsqueda de ${category}:`, err);
      setError(`Error al buscar ${category}: ${err.message}`);
      return null;
    } finally {
      setLoading(false);
    }
  };

  const buildPortfolio = async () => {
    setLoading(true);
    setError('');
    
    try {
      console.log('🚀 INICIANDO BÚSQUEDA EN TIEMPO REAL CON PERPLEXITY');
      console.log('📊 Perfil del inversor:', investmentProfile);
      console.log('💰 Asignación:', assetAllocation);
      
      const totalAmount = parseFloat(investmentProfile.amount);
      const promises = [];
      
      // Fetch data for each category based on allocation - EN TIEMPO REAL
      if (assetAllocation.value > 0) {
        const valueAmount = totalAmount * (assetAllocation.value / 100);
        console.log(`🔍 Buscando VALUE ETFs en tiempo real para $${valueAmount.toLocaleString()}`);
        promises.push(
          fetchPortfolioData('value', valueAmount)
            .then(data => ({ category: 'value', data }))
        );
      }
      
      if (assetAllocation.growth > 0) {
        const growthAmount = totalAmount * (assetAllocation.growth / 100);
        console.log(`🔍 Buscando GROWTH ETFs en tiempo real para $${growthAmount.toLocaleString()}`);
        promises.push(
          fetchPortfolioData('growth', growthAmount)
            .then(data => ({ category: 'growth', data }))
        );
      }
      
      if (assetAllocation.bonds > 0) {
        const bondsAmount = totalAmount * (assetAllocation.bonds / 100);
        console.log(`🔍 Buscando BONDS ETFs en tiempo real para $${bondsAmount.toLocaleString()}`);
        promises.push(
          fetchPortfolioData('bonds', bondsAmount)
            .then(data => ({ category: 'bonds', data }))
        );
      }
      
      if (assetAllocation.disruptive > 0) {
        const disruptiveAmount = totalAmount * (assetAllocation.disruptive / 100);
        console.log(`🔍 Buscando DISRUPTIVE ETFs en tiempo real para $${disruptiveAmount.toLocaleString()}`);
        promises.push(
          fetchPortfolioData('disruptive', disruptiveAmount)
            .then(data => ({ category: 'disruptive', data }))
        );
      }
      
      console.log('⏳ Ejecutando búsquedas paralelas en Perplexity...');
      const results = await Promise.all(promises);
      
      // Process results
      const newPortfolioData = { value: null, growth: null, bonds: null, disruptive: null };
      results.forEach(result => {
        if (result.data) {
          newPortfolioData[result.category] = result.data;
        }
      });
      
      setPortfolioData(newPortfolioData);
      
      // Create final portfolio
      const finalPortfolio = {
        allocation: {},
        totalAmount,
        assetAllocation,
        investmentProfile,
        createdAt: new Date().toISOString()
      };
      
      Object.entries(newPortfolioData).forEach(([category, data]) => {
        if (data && data.allocation) {
          finalPortfolio.allocation[category] = data.allocation;
        }
      });
      
      setFinalPortfolio(finalPortfolio);
      nextStep();
      
    } catch (err) {
      setError(err.message || 'Error al construir el portafolio');
    } finally {
      setLoading(false);
    }
  };

  const fetchClaudeAnalysis = useCallback(async () => {
    if (!finalPortfolio) return;
    
    setLoading(true);
    
    try {
  const response = await fetch(`${BASE_URL}/api/analysis/claude`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ portfolio: finalPortfolio })
      });
      
      if (!response.ok) {
        throw new Error('Error al obtener análisis de Claude');
      }
      
      const data = await response.json();
      setClaudeAnalysis(data.analysis || 'No se pudo obtener el análisis');
      
    } catch (err) {
      setClaudeAnalysis(`Error: ${err.message}`);
    } finally {
      setLoading(false);
    }
  }, [finalPortfolio, BASE_URL]);

  useEffect(() => {
    if (currentStep === 4 && finalPortfolio && !claudeAnalysis) {
      fetchClaudeAnalysis();
    }
  }, [currentStep, finalPortfolio, claudeAnalysis, fetchClaudeAnalysis]);

  const renderProgressSteps = () => (
    <div className="progress-steps">
      {steps.map((step, index) => (
        <div 
          key={step.id}
          className={`progress-step ${
            currentStep === step.id ? 'active' : 
            currentStep > step.id ? 'completed' : ''
          }`}
        >
          <div className="progress-step-circle">
            {currentStep > step.id ? '✓' : step.id}
          </div>
          <div className="progress-step-label">
            <div>{step.title}</div>
            <div style={{ fontSize: '0.8rem', opacity: 0.7 }}>{step.description}</div>
          </div>
        </div>
      ))}
    </div>
  );

  const renderStepContent = () => {
    switch (currentStep) {
      case 1:
        return (
          <InvestmentProfile 
            profile={investmentProfile}
            onUpdateProfile={setInvestmentProfile}
            onNext={nextStep}
          />
        );
      case 2:
        return (
          <AssetAllocation 
            allocation={assetAllocation}
            onUpdateAllocation={setAssetAllocation}
            onNext={nextStep}
            onPrev={prevStep}
          />
        );
      case 3:
        return (
          <div className="portfolio-construction">
            <div className="card">
              <div className="card-header">
                <h2 className="card-title">Construcción del Portafolio</h2>
              </div>
              <div className="construction-summary">
                <p>Monto total: <strong>${investmentProfile.amount.toLocaleString()}</strong></p>
                <p>Horizonte: <strong>{investmentProfile.horizon}</strong></p>
                
                <div className="real-time-notice" style={{
                  background: '#e6fffa',
                  border: '1px solid #38b2ac',
                  borderRadius: '8px',
                  padding: '16px',
                  marginBottom: '16px'
                }}>
                  <h4 style={{ color: '#2c7a7b', marginBottom: '8px', display: 'flex', alignItems: 'center' }}>
                    🔍 Búsqueda en Tiempo Real
                  </h4>
                  <p style={{ color: '#2c7a7b', margin: 0, fontSize: '0.9rem' }}>
                    Al hacer clic en "Buscar", Perplexity AI analizará el mercado en tiempo real 
                    para encontrar las mejores oportunidades de inversión según tu perfil y asignación.
                  </p>
                </div>

                <div className="allocation-summary">
                  <h4>Asignación de Activos:</h4>
                  <ul>
                    {Object.entries(assetAllocation).map(([key, value]) => (
                      value > 0 && (
                        <li key={key}>
                          <strong>{key.charAt(0).toUpperCase() + key.slice(1)}</strong>: {value}% 
                          (${(investmentProfile.amount * value / 100).toLocaleString()})
                        </li>
                      )
                    ))}
                  </ul>
                </div>
              </div>
              <div className="construction-actions">
                <button className="btn btn-secondary" onClick={prevStep}>
                  Anterior
                </button>
                <button 
                  className="btn" 
                  onClick={buildPortfolio}
                  disabled={loading}
                >
                  {loading ? 'Buscando en tiempo real con Perplexity...' : 'Buscar Activos en Tiempo Real'}
                </button>
              </div>
            </div>
          </div>
        );
      case 4:
        return (
          <div className="portfolio-analysis">
            <div className="tabs" style={{display:'flex', gap:8, margin:'0 0 12px 0'}}>
              <button className={`btn ${activeTab==='analysis'?'':'btn-secondary'}`} onClick={()=>setActiveTab('analysis')}>Análisis</button>
              <button className={`btn ${activeTab==='glossary'?'':'btn-secondary'}`} onClick={()=>setActiveTab('glossary')}>Glosario</button>
            </div>
            {activeTab==='analysis' ? (
              <>
                <PortfolioResults 
                  portfolio={finalPortfolio}
                  portfolioData={portfolioData}
                  showPrinciples={false}
                />
                <ClaudeAnalysis 
                  analysis={claudeAnalysis}
                  loading={loading}
                />
              </>
            ) : (
              <div className="card">
                <div className="card-header"><h3 className="card-title">Glosario y Principios del Value Investing</h3></div>
                <div className="card-body">
                  <p>Marco conceptual basado en Buffett y Munger:</p>
                  <ul>
                    <li><strong>Moat</strong>: Ventaja competitiva sostenible que protege la rentabilidad.</li>
                    <li><strong>Margen de seguridad</strong>: Descuento sobre valor intrínseco para reducir riesgo.</li>
                    <li><strong>ROE</strong>: Rentabilidad sobre patrimonio; preferible alto y consistente.</li>
                    <li><strong>Asignación de capital</strong>: Habilidad del management para reinvertir eficientemente.</li>
                    <li><strong>Disciplina</strong>: Paciencia para esperar precios atractivos.</li>
                  </ul>
                </div>
                <div className="card-body">
                  <h4>Principios Aplicados</h4>
                  <PortfolioResults 
                    portfolio={finalPortfolio}
                    portfolioData={portfolioData}
                    showPrinciples={true}
                  />
                </div>
              </div>
            )}
            <div className="analysis-actions">
              <button className="btn btn-secondary" onClick={prevStep}>
                Anterior
              </button>
              <button 
                className="btn" 
                onClick={() => {
                  setCurrentStep(1);
                  setFinalPortfolio(null);
                  setClaudeAnalysis('');
                  setPortfolioData({ value: null, growth: null, bonds: null, disruptive: null });
                }}
              >
                Nuevo Portafolio
              </button>
            </div>
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <div className="portfolio-builder">
      {renderProgressSteps()}
      
      {error && (
        <div className="error-message">
          {error}
        </div>
      )}
      
      {loading && (
        <LoadingSpinner message="Procesando..." />
      )}
      
      {renderStepContent()}
    </div>
  );
};

export default PortfolioBuilder; 