import React from 'react';
import { PieChart, Pie, Cell, ResponsiveContainer, BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';

const PortfolioResults = ({ portfolio, portfolioData, showPrinciples = true }) => {
  if (!portfolio || !portfolioData) {
    return (
      <div className="portfolio-results">
        <div className="card">
          <div className="card-header">
            <h2 className="card-title">Resultados del Portafolio</h2>
          </div>
          <div className="no-data">
            <p>No hay datos del portafolio para mostrar</p>
          </div>
        </div>
      </div>
    );
  }

  const COLORS = ['#4299e1', '#48bb78', '#ed8936', '#9f7aea'];
  
  // Prepare data for charts
  const prepareChartData = () => {
    const chartData = [];
    const categories = Object.keys(portfolioData).filter(key => portfolioData[key] && portfolioData[key].allocation);
    
    categories.forEach((category, index) => {
      const data = portfolioData[category];
      if (data && data.allocation) {
        const positions = Object.values(data.allocation).flat();
        const totalValue = positions.reduce((sum, pos) => sum + (pos.shares || 0) * (pos.price || 0), 0);
        
        chartData.push({
          name: category.charAt(0).toUpperCase() + category.slice(1),
          value: totalValue,
          percentage: portfolio.assetAllocation[category] || 0,
          positions: positions.length,
          color: COLORS[index % COLORS.length]
        });
      }
    });
    
    return chartData;
  };

  const chartData = prepareChartData();

  const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent }) => {
    const RADIAN = Math.PI / 180;
    const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
    const x = cx + radius * Math.cos(-midAngle * RADIAN);
    const y = cy + radius * Math.sin(-midAngle * RADIAN);

    return (
      <text x={x} y={y} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central">
        {`${(percent * 100).toFixed(0)}%`}
      </text>
    );
  };

  const renderPortfolioDetails = () => {
    const details = [];
    
    Object.entries(portfolioData).forEach(([category, data]) => {
      if (data && data.allocation) {
        const positions = Object.values(data.allocation).flat();
        if (positions.length > 0) {
          details.push({
            category,
            positions,
            totalValue: positions.reduce((sum, pos) => sum + (pos.shares || 0) * (pos.price || 0), 0)
          });
        }
      }
    });
    
    return details;
  };

  const portfolioDetails = renderPortfolioDetails();

  return (
    <div className="portfolio-results">
      {/* Portfolio Summary */}
      <div className="card">
        <div className="card-header">
          <h2 className="card-title">Resultados del Portafolio</h2>
          <div style={{
            background: '#e6fffa',
            border: '1px solid #38b2ac',
            borderRadius: '6px',
            padding: '12px',
            marginTop: '16px',
            display: 'flex',
            alignItems: 'center',
            gap: '8px'
          }}>
            <span style={{ fontSize: '1.2rem' }}>⚡</span>
            <div>
              <div style={{ color: '#2c7a7b', fontWeight: '600', fontSize: '0.9rem' }}>
                Datos obtenidos en tiempo real
              </div>
              <div style={{ color: '#2c7a7b', fontSize: '0.8rem' }}>
                Búsqueda completada: {new Date().toLocaleTimeString()}
              </div>
            </div>
          </div>
        </div>
        <div className="portfolio-summary">
          <div className="summary-stats">
            <div className="stat-item">
              <div className="stat-label">Monto Total</div>
              <div className="stat-value">${portfolio.totalAmount?.toLocaleString()}</div>
            </div>
            <div className="stat-item">
              <div className="stat-label">Categorías</div>
              <div className="stat-value">{chartData.length}</div>
            </div>
            <div className="stat-item">
              <div className="stat-label">Total Posiciones</div>
              <div className="stat-value">{chartData.reduce((sum, item) => sum + item.positions, 0)}</div>
            </div>
            <div className="stat-item">
              <div className="stat-label">Horizonte</div>
              <div className="stat-value">{portfolio.investmentProfile?.horizon}</div>
            </div>
          </div>
        </div>
      </div>

      {/* Charts */}
      <div className="charts-container">
        <div className="card">
          <div className="card-header">
            <h3 className="card-title">Distribución por Categorías</h3>
          </div>
          <div className="chart-content">
            <ResponsiveContainer width="100%" height={300}>
              <PieChart>
                <Pie
                  data={chartData}
                  cx="50%"
                  cy="50%"
                  labelLine={false}
                  label={renderCustomizedLabel}
                  outerRadius={80}
                  fill="#8884d8"
                  dataKey="percentage"
                >
                  {chartData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.color} />
                  ))}
                </Pie>
                <Tooltip formatter={(value) => `${value}%`} />
              </PieChart>
            </ResponsiveContainer>
          </div>
        </div>

        <div className="card">
          <div className="card-header">
            <h3 className="card-title">Número de Posiciones por Categoría</h3>
          </div>
          <div className="chart-content">
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={chartData}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="name" />
                <YAxis />
                <Tooltip />
                <Legend />
                <Bar dataKey="positions" fill="#667eea" />
              </BarChart>
            </ResponsiveContainer>
          </div>
        </div>
      </div>

      {/* Portfolio Details */}
      <div className="portfolio-details">
        <div className="card">
          <div className="card-header">
            <h3 className="card-title">Detalle de Posiciones</h3>
          </div>
          <div className="details-content">
            {portfolioDetails.map(({ category, positions, totalValue }) => (
              <div key={category} className="category-section">
                <div className="category-header">
                  <h4>{category.charAt(0).toUpperCase() + category.slice(1)}</h4>
                  <div className="category-stats">
                    <span className="stat">
                      {positions.length} posiciones
                    </span>
                    <span className="stat">
                      ${totalValue.toLocaleString()}
                    </span>
                  </div>
                </div>
                <div className="positions-table-container">
                  <table className="positions-table">
                    <thead>
                      <tr>
                        <th>Símbolo</th>
                        <th>Nombre</th>
                        <th>Precio</th>
                        <th>Acciones</th>
                        <th>Valor Total</th>
                      </tr>
                    </thead>
                    <tbody>
                      {positions.map((position, index) => (
                        <tr key={index}>
                          <td>
                            <strong className="symbol">{position.symbol || 'N/A'}</strong>
                          </td>
                          <td className="name">{position.name || 'N/A'}</td>
                          <td className="price">${position.price?.toFixed(2) || 'N/A'}</td>
                          <td className="shares">{position.shares || 'N/A'}</td>
                          <td className="total-value">
                            <strong>${((position.shares || 0) * (position.price || 0)).toFixed(2)}</strong>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Value Investing Principles (optional) */}
      {showPrinciples && (
        <div className="card">
          <div className="card-header">
            <h3 className="card-title">Principios de Value Investing Aplicados</h3>
          </div>
          <div className="principles-content">
            <div className="principles-grid">
              <div className="principle-item">
                <div className="principle-icon">🏰</div>
                <h4>Ventaja Competitiva Duradera</h4>
                <p>Las empresas seleccionadas poseen ventajas competitivas sostenibles como marcas fuertes, efectos de red, o ventajas de coste.</p>
              </div>
              <div className="principle-item">
                <div className="principle-icon">👥</div>
                <h4>Gestión de Calidad</h4>
                <p>Se priorizaron empresas con equipos directivos que han demostrado asignación eficiente de capital y alineación con los intereses de los accionistas.</p>
              </div>
              <div className="principle-item">
                <div className="principle-icon">🛡️</div>
                <h4>Margen de Seguridad</h4>
                <p>Todas las acciones fueron seleccionadas con un precio por debajo de su valor intrínseco estimado, proporcionando un margen de seguridad.</p>
              </div>
              <div className="principle-item">
                <div className="principle-icon">🌍</div>
                <h4>Diversificación Inteligente</h4>
                <p>El portafolio incluye exposición a diferentes sectores y mercados geográficos, manteniendo concentración en las mejores ideas.</p>
              </div>
              <div className="principle-item">
                <div className="principle-icon">📈</div>
                <h4>Horizonte a Largo Plazo</h4>
                <p>Las empresas fueron seleccionadas con una perspectiva de inversión de 7+ años, ignorando fluctuaciones a corto plazo.</p>
              </div>
              <div className="principle-item">
                <div className="principle-icon">💰</div>
                <h4>Valor Intrínseco</h4>
                <p>Cada inversión está basada en el análisis fundamental y la evaluación del valor intrínseco de la empresa.</p>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default PortfolioResults; 