import React from 'react';

const InvestmentProfile = ({ profile, onUpdateProfile, onNext }) => {
  const handleInputChange = (field, value) => {
    onUpdateProfile(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleNext = () => {
    if (profile.amount && profile.amount > 0) {
      onNext();
    }
  };

  return (
    <div className="investment-profile">
      <div className="card">
        <div className="card-header">
          <h2 className="card-title">Perfil de Inversión</h2>
          <p>Comparte tu información para crear un portafolio personalizado</p>
        </div>
        
        <div className="form-section">
          <div className="form-group">
            <label htmlFor="amount">
              Monto a invertir (USD) *
            </label>
            <input
              id="amount"
              type="number"
              value={profile.amount}
              onChange={(e) => handleInputChange('amount', parseFloat(e.target.value) || 0)}
              placeholder="10000"
              min="1000"
              step="1000"
            />
            <small>Mínimo recomendado: $1,000</small>
          </div>

          <div className="form-group">
            <label htmlFor="horizon">
              Horizonte de inversión *
            </label>
            <select
              id="horizon"
              value={profile.horizon}
              onChange={(e) => handleInputChange('horizon', e.target.value)}
            >
              <option value="corto">Corto plazo (1-3 años)</option>
              <option value="intermedio">Intermedio (3-7 años)</option>
              <option value="largo">Largo plazo (7+ años)</option>
            </select>
          </div>

          <div className="form-group">
            <label htmlFor="riskTolerance">
              Tolerancia al riesgo
            </label>
            <select
              id="riskTolerance"
              value={profile.riskTolerance}
              onChange={(e) => handleInputChange('riskTolerance', e.target.value)}
            >
              <option value="conservador">Conservador</option>
              <option value="moderado">Moderado</option>
              <option value="agresivo">Agresivo</option>
            </select>
          </div>

          <div className="form-group">
            <label htmlFor="experience">
              Experiencia en inversiones
            </label>
            <select
              id="experience"
              value={profile.experience}
              onChange={(e) => handleInputChange('experience', e.target.value)}
            >
              <option value="principiante">Principiante</option>
              <option value="intermedio">Intermedio</option>
              <option value="avanzado">Avanzado</option>
            </select>
          </div>
        </div>

        <div className="profile-summary">
          <h4>Resumen del Perfil</h4>
          <div className="summary-grid">
            <div className="summary-item">
              <span className="label">Monto:</span>
              <span className="value">${profile.amount.toLocaleString()}</span>
            </div>
            <div className="summary-item">
              <span className="label">Horizonte:</span>
              <span className="value">{profile.horizon}</span>
            </div>
            <div className="summary-item">
              <span className="label">Riesgo:</span>
              <span className="value">{profile.riskTolerance}</span>
            </div>
            <div className="summary-item">
              <span className="label">Experiencia:</span>
              <span className="value">{profile.experience}</span>
            </div>
          </div>
        </div>

        <div className="form-actions">
          <button 
            className="btn" 
            onClick={handleNext}
            disabled={!profile.amount || profile.amount < 1000}
          >
            Continuar
          </button>
        </div>
      </div>
    </div>
  );
};

export default InvestmentProfile; 