import React from 'react';

const AssetAllocation = ({ allocation, onUpdateAllocation, onNext, onPrev }) => {
  const handleAllocationChange = (category, value) => {
    const newValue = Math.max(0, Math.min(100, parseInt(value) || 0));
    onUpdateAllocation(prev => ({
      ...prev,
      [category]: newValue
    }));
  };

  const getTotalAllocation = () => {
    return Object.values(allocation).reduce((sum, value) => sum + value, 0);
  };

  const getRecommendedAllocation = (horizon, riskTolerance) => {
    // Simplified allocation recommendations
    const recommendations = {
      'corto': { bonds: 60, value: 30, growth: 10, disruptive: 0 },
      'intermedio': { bonds: 40, value: 40, growth: 20, disruptive: 0 },
      'largo': { bonds: 20, value: 50, growth: 25, disruptive: 5 }
    };
    return recommendations[horizon] || recommendations['largo'];
  };

  const applyRecommendedAllocation = (recommendedAllocation) => {
    onUpdateAllocation(recommendedAllocation);
  };

  const totalAllocation = getTotalAllocation();
  const isValidAllocation = totalAllocation === 100;

  return (
    <div className="asset-allocation">
      <div className="card">
        <div className="card-header">
          <h2 className="card-title">Asignación de Activos</h2>
          <p>Define cómo distribuir tu inversión entre diferentes tipos de activos</p>
        </div>

        <div className="allocation-controls">
          <div className="allocation-input-group">
            <div className="allocation-input">
              <label htmlFor="bonds">
                Bonos y Renta Fija
                <span className="allocation-description">Inversiones conservadoras con rendimiento fijo</span>
              </label>
              <div className="input-with-slider">
                <input
                  id="bonds"
                  type="range"
                  min="0"
                  max="100"
                  value={allocation.bonds}
                  onChange={(e) => handleAllocationChange('bonds', e.target.value)}
                  className="slider"
                />
                <input
                  type="number"
                  value={allocation.bonds}
                  onChange={(e) => handleAllocationChange('bonds', e.target.value)}
                  className="percentage-input"
                  min="0"
                  max="100"
                />
                <span className="percentage-label">%</span>
              </div>
            </div>

            <div className="allocation-input">
              <label htmlFor="value">
                Value (Valor)
                <span className="allocation-description">Empresas infravaloradas con fundamentos sólidos</span>
              </label>
              <div className="input-with-slider">
                <input
                  id="value"
                  type="range"
                  min="0"
                  max="100"
                  value={allocation.value}
                  onChange={(e) => handleAllocationChange('value', e.target.value)}
                  className="slider"
                />
                <input
                  type="number"
                  value={allocation.value}
                  onChange={(e) => handleAllocationChange('value', e.target.value)}
                  className="percentage-input"
                  min="0"
                  max="100"
                />
                <span className="percentage-label">%</span>
              </div>
            </div>

            <div className="allocation-input">
              <label htmlFor="growth">
                Growth (Crecimiento)
                <span className="allocation-description">Empresas con alto potencial de crecimiento</span>
              </label>
              <div className="input-with-slider">
                <input
                  id="growth"
                  type="range"
                  min="0"
                  max="100"
                  value={allocation.growth}
                  onChange={(e) => handleAllocationChange('growth', e.target.value)}
                  className="slider"
                />
                <input
                  type="number"
                  value={allocation.growth}
                  onChange={(e) => handleAllocationChange('growth', e.target.value)}
                  className="percentage-input"
                  min="0"
                  max="100"
                />
                <span className="percentage-label">%</span>
              </div>
            </div>

            <div className="allocation-input">
              <label htmlFor="disruptive">
                Disruptivas
                <span className="allocation-description">Tecnologías emergentes y sectores innovadores</span>
              </label>
              <div className="input-with-slider">
                <input
                  id="disruptive"
                  type="range"
                  min="0"
                  max="100"
                  value={allocation.disruptive}
                  onChange={(e) => handleAllocationChange('disruptive', e.target.value)}
                  className="slider"
                />
                <input
                  type="number"
                  value={allocation.disruptive}
                  onChange={(e) => handleAllocationChange('disruptive', e.target.value)}
                  className="percentage-input"
                  min="0"
                  max="100"
                />
                <span className="percentage-label">%</span>
              </div>
            </div>
          </div>

          <div className="allocation-summary">
            <div className="total-allocation">
              <h4>Total: {totalAllocation}%</h4>
              <div className={`allocation-status ${isValidAllocation ? 'valid' : 'invalid'}`}>
                {isValidAllocation ? '✓ Asignación válida' : '⚠️ Debe sumar 100%'}
              </div>
            </div>

            <div className="allocation-visualization">
              <div className="allocation-bar">
                <div className="allocation-segment bonds" style={{ width: `${allocation.bonds}%` }}></div>
                <div className="allocation-segment value" style={{ width: `${allocation.value}%` }}></div>
                <div className="allocation-segment growth" style={{ width: `${allocation.growth}%` }}></div>
                <div className="allocation-segment disruptive" style={{ width: `${allocation.disruptive}%` }}></div>
              </div>
              <div className="allocation-legend">
                <div className="legend-item">
                  <span className="legend-color bonds"></span>
                  <span>Bonos ({allocation.bonds}%)</span>
                </div>
                <div className="legend-item">
                  <span className="legend-color value"></span>
                  <span>Value ({allocation.value}%)</span>
                </div>
                <div className="legend-item">
                  <span className="legend-color growth"></span>
                  <span>Growth ({allocation.growth}%)</span>
                </div>
                <div className="legend-item">
                  <span className="legend-color disruptive"></span>
                  <span>Disruptivas ({allocation.disruptive}%)</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="recommended-allocations">
          <h4>Asignaciones Recomendadas</h4>
          <div className="recommendations-grid">
            {['corto', 'intermedio', 'largo'].map(horizon => {
              const recommended = getRecommendedAllocation(horizon);
              return (
                <div key={horizon} className="recommendation-card">
                  <h5>{horizon.charAt(0).toUpperCase() + horizon.slice(1)} plazo</h5>
                  <div className="recommendation-breakdown">
                    <div>Bonos: {recommended.bonds}%</div>
                    <div>Value: {recommended.value}%</div>
                    <div>Growth: {recommended.growth}%</div>
                    <div>Disruptivas: {recommended.disruptive}%</div>
                  </div>
                  <button 
                    className="btn btn-secondary"
                    onClick={() => applyRecommendedAllocation(recommended)}
                  >
                    Aplicar
                  </button>
                </div>
              );
            })}
          </div>
        </div>

        <div className="form-actions">
          <button className="btn btn-secondary" onClick={onPrev}>
            Anterior
          </button>
          <button 
            className="btn" 
            onClick={onNext}
            disabled={!isValidAllocation}
          >
            Continuar
          </button>
        </div>
      </div>
    </div>
  );
};

export default AssetAllocation; 